import { Injectable, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpErrorResponse, HttpEvent, HttpResponseBase, HttpResponse } from '@angular/common/http';
import { Observable, of, throwError } from 'rxjs';
import { mergeMap, catchError } from 'rxjs/operators';
import { NzMessageService, NzNotificationService } from 'ng-zorro-antd';
import { _HttpClient } from '@delon/theme';
import { environment } from '@env/environment';
import { DA_SERVICE_TOKEN, ITokenService } from '@delon/auth';
const CODEMESSAGE = {
    200: '',
    201: '',
    202: '',
    204: '',
    400: '',
    401: '',
    403: '',
    404: '',
    406: '',
    410: '',
    422: '',
    500: '',
    502: '',
    503: '',
    504: '',
};

/**
 * HTTP `app.module.ts`
 */
@Injectable()
export class DefaultInterceptor implements HttpInterceptor {
    constructor(
        public msgSrv: NzMessageService,

        private injector: Injector) { }

    get msg(): NzMessageService {
        return this.injector.get(NzMessageService);
    }

    private goTo(url: string) {
        setTimeout(() => this.injector.get(Router).navigateByUrl(url));
    }

    private checkStatus(ev: HttpResponseBase) {
        if (ev.status >= 200 && ev.status < 300) return;

        const errortext = CODEMESSAGE[ev.status] || ev.statusText;
        this.injector.get(NzNotificationService).error(
            ` ${ev.status}: ${ev.url}`,
            errortext
        );
    }
    /**
     * ,
     * @param data
     */
    private isMyFor(data: any): boolean {
        if (data && data.hasOwnProperty('code')) {
            return true;
        }
        return false;
    }

    private handleData(ev: HttpResponseBase): Observable<any> {
        //  `throw`  `_HttpClient`  `end()` 
        if (ev.status > 0) {
            this.injector.get(_HttpClient).end();
        }
        this.checkStatus(ev);
        // 
        switch (ev.status) {
            case 200:
                // restful
                // 
                //  { status: 1, msg: '' }
                //  { status: 0, response: {  } }
                // 
                if (event instanceof HttpResponse) {
                   const body: any = event.body;
                   if (this.isMyFor(body)) {
                       if (body.code == 200) {
                           //200,,data
                           return of(new HttpResponse(Object.assign(event, { body: body.data })));
                       } else {
                           //200,
                           //,.
                           //,
                           this.msgSrv.info(body.message)
                       }
                   } else {
                       return of(event);
                   }
                }
                //    //if (body && body.status !== 0) {
                //    //    this.msg.error(body.msg);
                //    //    //  Pipesubscribe 
                //    //    // this.http.get('/').subscribe() 
                //    //    return throwError({});
                //    //} else {
                //    //    //  `body`  `response` 
                //    //    return of(new HttpResponse(Object.assign(event, { body: body.response })));
                //    //    // 
                //    //    return of(event);
                //    //}
                //}
                break;
            case 401: // 
                //  401: https://preview.pro.ant.design/api/401 
                (this.injector.get(DA_SERVICE_TOKEN) as ITokenService).clear();
                this.goTo('/passport/login');
                break;
            case 403:
            case 404:
            case 500:
                this.goTo(`/exception/${ev.status}`);
                break;
            default:
                if (ev instanceof HttpErrorResponse) {
                    console.warn('CORS', ev);
                    return throwError(ev);
                }
                break;
        }
        return of(ev);
    }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        // 
        let url = req.url;
        if (!url.startsWith('https://') && !url.startsWith('http://')) {
            //url = environment.SERVER_URL + url;
            if (url.startsWith('/') || url.startsWith('\\')) {
                url = environment.SERVER_URL + url;
            } else {
                url = './' + url;
            }
        }


        const newReq = req.clone({ url });
        return next.handle(newReq).pipe(
            mergeMap((event: any) => {
                // 
                if (event instanceof HttpResponseBase)
                    return this.handleData(event);
                // 
                return of(event);
            }),
            catchError((err: HttpErrorResponse) => this.handleData(err)),
        );
    }
}
